import {BrowserRouter, Routes, Route} from "react-router-dom";
import Landing from "./components/landing/Index";
import SuperAdminLanding from "./components/admin/Landing";
import EventLanding from "./components/pages/EventLanding";

const AbstractSuiteRoutes = () =>{
    return (
        <BrowserRouter>
            <Routes>
                <Route exact path="/" element={<Landing />}/>
                <Route exact path="/super-admin" element={<SuperAdminLanding />}/>
                <Route exact path="/:event_uri" element={<EventLanding />}/>
            </Routes>
        </BrowserRouter>
    );
}

export default AbstractSuiteRoutes;