import {useParams} from "react-router-dom";
import useApi from "../../hooks/use-api";
import {useEffect, useState} from "react";
// import LoadingOverlay from "./common/LoadingOverlay";
import EventAppBar from "./common/EventAppBar";
import Container from "@mui/material/Container";
import {Box, Grid, Skeleton} from "@mui/material";
import Alert from "@mui/material/Alert";
import Button from "@mui/material/Button";
import Footer from "./common/Footer";
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
/** End of imports **/

const errorSwal = withReactContent(Swal);

const EventLanding = () => {
    let {event_uri} = useParams();
    const {isLoading, error, sendRequest:getEventDetails} = useApi();
    const [appBarColors, setAppBarColors] = useState({primary:'#0c80b0', secondary:'#214c80'});
    const [bannerLoading, setBannerLoading] = useState(true);
    const [mainBanner, setMainBanner] = useState("");
    const [eventName, setEventName] = useState(()=><Skeleton variant="text" width={400}/>);
    const [eventLocation, setEventLocation] = useState(()=><Skeleton variant="text" width={300}/>);
    const [eventDates, setEventDates] = useState(()=><Skeleton variant="text" width={300}/>);
    const [eventHomeContent, setEventHomeContent] = useState("<div></div>");

    useEffect(()=>{
        const getEventDetailsCallback = (response) => {
            if(response.status !== 200)
            {
                switch (response.status) {
                    case 403:
                        errorSwal.fire({
                            icon: 'error',
                            title: `/${event_uri}`,
                            html: `We couldn't find that event. <br>Please check the URL and try again.`,
                            confirmButtonText: `Demo App <i class="fa-solid fa-desktop"></i>`,
                            allowOutsideClick: false,
                        }).then(() => {
                            window.location = "/demo";
                        });
                        break;
                    default:
                        console.log('Unknown error');
                        break;
                }
                return false;
            }

            let details = response.data.data;

            document.title = details.short_name+" | Abstract Suite";
            setAppBarColors({primary:'#2aa69c', secondary:'#437143'});
            setMainBanner(details.main_banner);
            setEventName(details.name);
            setEventLocation(`${details.city}, ${details.state}`);
            setEventHomeContent(details.home_content);

            let startDate = new Date(details.start_timestamp * 1000);
            let endDate = new Date(details.end_timestamp * 1000);
            let month = startDate.toLocaleString('default', { month: 'long' });
            let eventDate = `${month} ${startDate.getDate()}-${endDate.getDate()}, ${startDate.getFullYear()}`
            setEventDates(eventDate);
        };
        getEventDetails({path:`event/details/${event_uri}`, method: "GET"}, getEventDetailsCallback).then(()=>{
        });
    }, [])// eslint-disable-line react-hooks/exhaustive-deps


    // JSX Contents
    return (
      <>
          {/*<LoadingOverlay isLoading={isLoading}/>*/}

          { (isLoading||error) ? <Skeleton variant="text" height={100}/> : null }
          { (!isLoading&&!error) ? <EventAppBar eventName={eventName} colors={appBarColors}/> : null }

          <Container maxWidth="100%" style={{width: "98%", marginTop:"20px", boxShadow:"0px 0px 25px #999"}}>
              <Grid container spacing={2}>
                  <Grid item xs={12}>

                      <Box style={{marginBottom:"10px"}}>
                          <Skeleton variant="rectangular" width="100%" height="25vh" style={{display:(bannerLoading||error)?'':'none'}}/>
                          <img
                              style={{display:(!bannerLoading&&!error)?'':'none'}}
                              src={mainBanner} width="100%"
                              alt="Main Banner"
                              onError={() => setBannerLoading(true)}
                              onLoad={() => setBannerLoading(false)}
                          />
                      </Box>

                      { (isLoading||error) ? <Skeleton variant="text" height={50}/> : null }
                      { (!isLoading&&!error) ?
                          <Box>
                              <h2 style={{marginBottom:"8px"}}>{eventName}</h2>
                              <h4 style={{marginBottom:"1px", marginTop:"1px"}}>{eventDates}</h4>
                              <h4 style={{marginTop:"1px"}}>{eventLocation}</h4>
                          </Box>
                          : null
                      }

                      { (isLoading||error) ? <Skeleton variant="text" height={100}/> : null }
                      { (isLoading||error) ? <Skeleton variant="text" height={100}/> : null }
                      { (isLoading||error) ? <Skeleton variant="rectangular" height={1000}/> : null }
                      { (!isLoading&&!error) ?
                          <Box>
                              <hr/>
                              <Alert severity="error" icon={false} sx={{display:'block', textAlign:'center'}}>
                                  <strong> Abstract Submissions Deadline: May 31, 2023 5pm MT EST</strong>
                              </Alert>

                              <Box style={{marginTop:'10px'}}>
                                  <Button variant="contained" size="large" style={{width:'100%'}}>Click here to submit/edit abstracts</Button>
                              </Box>

                              <hr/>
                              <Box style={{marginBottom:'10px', paddingBottom:'25px', height:'100%', background:"#eeeeee"}}>
                                  <Container maxWidth="99%" style={{paddingTop:"30px"}} dangerouslySetInnerHTML={{ __html: eventHomeContent }} />
                              </Box>
                              <Box style={{marginTop:'10px'}}>
                                  <Button variant="contained" size="large" style={{width:'100%'}}>Click here to submit/edit abstracts</Button>
                              </Box>
                              <hr/>
                          </Box>
                          : null
                      }

                  </Grid>
              </Grid>
          </Container>
          <Footer/>

      </>
    );
}

export default EventLanding;