/**
 * Abstract Suite API request hook
 * @author Athul AK <athulak.mail@gmail.com>
 */

import {useCallback, useState} from "react";
import axios from "axios";

const API_ENDPOINT = `${process.env.REACT_APP_API_ENDPOINT}/${process.env.REACT_APP_API_VERSION}`;


const getToken = () =>{
    let token = localStorage.getItem('abstract_suite_token');
    return token??false;
};

/**
 * @returns {{isLoading: boolean, error: unknown, sendRequest: ((function(): Promise<void>)|*)}}
 */
const useApi = () => {
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(false);

    /**
     * @param request {{path: string, method: string, header: {}, body: {}, mode: string}}
     * @param callback {{function()}}
     * @type {(function(*): Promise<void>)|*}
     */
    const sendRequest = useCallback(async (request, callback) =>{
        setIsLoading(true);
        setError(false);
        request.mode = 'no-cors';

        if (request.path !== "" && !request.path.startsWith("/"))
        {
            request.path = "/"+request.path;
        }

        const options = {
            headers:
                {
                    'Authorization': 'Bearer ' + getToken()
                },
            method: request.method,
            url: `${API_ENDPOINT}${request.path}`,
            data: request.body
        };

        try{
            const response = await axios(options);
            if (response.status !== 200)
            {

                throw new Error(response);
            }

            await response;
            callback(response);
        }catch (e) {
            setError(e.response);
            callback(e.response);

            if(process.env.REACT_APP_ENV !== 'production')
            {
                console.log(e.response)
            }

            if (e.response.status === 404) {
                //console.log("404 Error")
            }else if(e.response.status === 403) {
                //console.log(e.response.statusText)
            }
        }finally {
            setIsLoading(false);
        }
    }, []);

    return {
        isLoading,
        error,
        sendRequest
    };

};

export default useApi;